.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 40px 120px;
    color: white;
}

.nav-logo{
    font-family: Outfit;
    font-size: 50px;
    font-weight: 500;
    text-shadow: 0 0 5px #000000;
}

.nav-logo:hover{
    color: yellow;
}

.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    font-size: 23px;
    gap: 90px;
    text-shadow: 0 0 5px #000000;
}

.nav-contact{
    border-radius: 50px;
    padding: 10px 38px;
    background: #fff;
    color: #262626;
}

/* styles.css */
.nav-link {
    text-decoration: none;
    color: inherit; 
    padding: 8px 16px;
    display: inline-block;
  }
  
  .nav-link:hover {
    color: yellow; 
  }
